import React from 'react';
import { Button, Col, Container, Row } from "react-bootstrap";
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import wheel from '../../Images/wheels.png';
import car from '../../Images/car.png';
import space from '../../Images/space.png';
import Form from 'react-bootstrap/Form';
import './Tyrewale.css';

import { useNavigate } from 'react-router-dom';

export default function Tyrewale(){
     const navigate=useNavigate();
     const [key, setKey] = React.useState('TYRES SIZE');
     const[brand,setBrand]=React.useState("");
  
      const onHandleChange=(e)=>{
        setBrand(e.target.value);
      }
   
      const onClickHandler=(e)=>{
         if(brand === "Bridgestone"){
            navigate('/bridgestonePage')
         }
         if(brand === "Ceat"){
            navigate('/ceatPage')
         }
         if(brand === "Pirelli"){
            navigate('/pirelliPage')
         }
         if(brand === "Yokohama"){
            navigate('/yokohamaPage')
         }
      }
    return(
        <>
           <Container className="tabs-container">
              <Row>
               <Col md={{offset:2}}>
                  <Col md={8} lg={10}>
                     <div className='effect-style' style={{zIndex:'9 !important',borderRadius:'25px 25px 0 0'}}>
                     <Tabs
                        id="controlled-tab-example"
                        activeKey={key}
                        onSelect={(k) => setKey(k)}
                     >
                    <Tab eventKey="TYRES SIZE" 
                        title={<span> <img src={wheel} alt="" style={{width:'29px'}}/>&nbsp;TYRES&nbsp;SIZE</span>} 
                     >
                        <div className='tab-content'>
                           <div className='mt-20'>
                              <Row style={{zIndex:'999999!important'}}>
                                 <Col xs={4} md={4} sm={4} >
                                  <div className='select-style mb-30'>
                                   <Form.Select size="sm" onChange={onHandleChange} value={brand}>
                                       <option value="">Select Brand</option>
                                       <option value="Bridgestone">BRIDGESTONE</option>
                                       <option value="Ceat">CEAT</option>
                                       <option value="Pirelli">PIRELLI</option>
                                       <option value="Yokohama">YOKOHAMA</option>
                                       <option value="Michelin">MICHELIN</option>
                                       <option value="Continental">CONTINENTAL</option>
                                   </Form.Select>
                                   </div>
                                 </Col>
                                 <Col xs={4} md={4} sm={4}>
                                  <div className='select-style mb-30'>
                                   <Form.Select size="sm">
                                       <option>Profile</option>
                                   </Form.Select>
                                  </div>
                                 </Col>
                                 <Col xs={4} md={4} sm={4}>
                                  <div className='select-style mb-30'>
                                   <Form.Select size="sm">
                                       <option>Rim</option>
                                   </Form.Select>
                                   </div>
                                 </Col>
                              </Row>
                              <div className='text-center'>
                                <img id="curve-image-style" src={space} alt=""/>
                              </div>
                              <Row>
                                 <Col md={12}>
                                    <div className='mb-15 d-grid'>
                                       
                                       <Button  className='button-style' onClick={onClickHandler}>Search Tyres</Button>
                                       
                                       </div>
                                 </Col>
                              </Row>
                           </div>
                        </div>
                     </Tab>
                    <Tab eventKey="CAR BRANDS" 
                        title={<span> CAR&nbsp;BRANDS&nbsp;<img src={car} alt=""/></span>}
                        className="tabs-style"
                     >
                        <div className='tab-content'>
                           <div className='mt-20'>
                              <Row>
                                 <Col md={6} sm={6}>
                                    <div className='select-style mb-30'>
                                       <Form.Select size="sm">
                                           <option>Select Manufacturer</option>
                                       </Form.Select>
                                    </div>
                                 </Col>
                                 <Col md={6} sm={6}>
                                    <div className='select-style mb-30'>
                                       <Form.Select size="sm">
                                           <option>Select Vehicle Model</option>
                                       </Form.Select>
                                    </div>
                                 </Col>
                              </Row>
                           </div>
                        </div>
                   </Tab>
                  </Tabs>
                  </div>
                  </Col>

               </Col>
              </Row>
             
           </Container>
         
        </>
    )
}