import React, { useEffect } from 'react';
import { Container, Row } from "react-bootstrap";
import { connect } from 'react-redux';
import { getYokohamaDetails, getYokohamaList } from '../Redux/Action/Action';
import YokohamaItem from './YokohamaItem';

 function Yokohama(props){

    useEffect(()=>{
            fetch('http://localhost:3000/Yokohama')
            .then(response=>response.json())
            .then((res)=>{
                props.setYokohamaList(res);
            })
    },[])
    return(
        <>
         <Container>
            <Row>
                {
                   props.yokohamaList && props.yokohamaList.map((item)=>{
                        return  <YokohamaItem product={item}></YokohamaItem> 
                    })
                }
               
            </Row>
         </Container>
        </>
    )
}
    const mapStateToProps=(state)=>{
        return{
            yokohamaList:state.productFeature.yokohamaList
        }
    }
    const mapDispatchToProps=(dispatch)=>{
        return{
            setYokohamaList:(payload) => dispatch(getYokohamaList(payload)),
            setYokohamaDetails:(payload) => dispatch(getYokohamaDetails(payload))
        }
    }

export default connect(mapStateToProps,mapDispatchToProps)(Yokohama)