import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getPirelliDetails } from '../Redux/Action/Action';
import PirelliDesc from './PirelliDesc';

 function PirelliDetail(props){
    const {id}=useParams();

    useEffect(()=>{
        fetch('http://localhost:3000/Pirelli/'+id)
        .then(response=>response.json())
        .then((res)=>{
            props.setPirelliDetails(res);
        })
    },[])  
    return(
        <>
        
            {
                props.pirelliDetails && 
                <PirelliDesc isProductDetails={true} product={props.pirelliDetails}></PirelliDesc>
            }
         
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        pirelliDetails:state.productFeature.pirelliDetails
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setPirelliDetails: (payload) => dispatch(getPirelliDetails(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(PirelliDetail)