import React from 'react';
import Form from 'react-bootstrap/Form';
import { Col, Container,Row } from "react-bootstrap";
import logo from '../../Images/logo.png';
import './LogIn.css';
import { Link, useNavigate } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import { connect } from 'react-redux';
import { setEmailAddress, setLoginSuccess, setPassword } from '../Redux/Action/Action';

 function LogIn(props){
    const navigate=useNavigate();
    const handleForm=(e)=>{
           e.preventDefault();
           return true;
    }
   

    const onChangeEmail=(e)=>{
      props.setEmail(e.target.value)
    }
    const onChangePassword=(e)=>{
      props.setPassord(e.target.value)
    }

    const loginForm=()=>{
      var loginId={
        ...props.userLogin,
        id:1
      }
        fetch('http://localhost:3000/logins',{
          method:"POST",
          body: JSON.stringify(loginId),
        })
        .then(response=>response.json())
        .then((res)=>{
           console.log(res);
          props.setLoginSuccess(true);
           localStorage.setItem("isLoggedIn",true);
           navigate('/')
        });
        return false
    }
    return(
        <>
          <div className='login-wrapper'>
            <Container >
              <Row>
                <Col md={12} style={{textAlign:'center',padding:'14px'}}>
                   <img src={logo} style={{width:'130px'}} alt="tyrewale"/>
                </Col>
                <Col md={12} >
                    <div className='login-style'>
                      <h1 style={{fontSize:'28px'}}>Sign-In</h1>
                      <Form  onSubmit={handleForm}>
                        <Form.Group className="mb-3" >
                          <Form.Label>Email Address</Form.Label>
                            <Form.Control
                              type='email'
                              placeholder="Email"
                              value={props.userLogin.email}
                              onChange={onChangeEmail}
                              required
                            />
                         </Form.Group>
                         <Form.Group className="mb-3" >
                            <Form.Label >Password</Form.Label>
                                <Form.Control
                                  type='password'
                                  placeholder="Password"
                                  aria-label="Password"
                                  aria-describedby="basic-addon2"
                                  value={props.userLogin.passord}
                                  onChange={onChangePassword}
                                  required
                                />
                           </Form.Group>
                        <div className="mb-3" >
                          <Row>
                           <Col xs={6}>
                              <Form.Group  controlId="formBasicCheckbox" >
                                  <Form.Check type="checkbox" label="Remember me"/>
                               </Form.Group>
                            </Col>
                            <Col xs={6} className="text-center">
                                <Link to="#" style={{fontSize:'12px'}}>Forget password</Link>
                            </Col>
                          </Row>
                        </div> 
                        <div >
                          <Row style={{marginBottom:'50px'}}>
                            <Col  md={12}>
                              <Button as="input" type="submit" value="Login" className="login-button" 
                              onClick={loginForm}/>
                            </Col>
                          </Row>
                        </div>
                        <div className='tbs-divider'></div>
                        <div className="text-center">
                           <p className="login-text">Don't have an account ?&nbsp;<Link to="/signup">Sign up</Link></p>
                        </div>
                      </Form>
                    </div>
                </Col>
              </Row>
            </Container>
          </div>
        </>
    )
}
const mapStateToProps=(state)=>{
  return{
    userLogin:state.productFeature.userLogin
  }
}
const mapDispatchToProps=(dispatch)=>{
   return{
      setEmail:(payload)=>dispatch(setEmailAddress(payload)),
      setPassord:(payload)=>dispatch(setPassword(payload)),
      setLoginSuccess:(payload)=>dispatch(setLoginSuccess(payload))
   }
}
export default connect(mapStateToProps,mapDispatchToProps)(LogIn)