import React from 'react';
import { Col } from "react-bootstrap";
import { Link } from 'react-router-dom';
import rupee1 from '../../Images/rupee1.png';
import Button from 'react-bootstrap/Button';
import './Yokohama.css';

export default function YokohamaItem(props){
    return(
        <>
          <Col xs={6} md={3} lg={3} sm={4} className="product product-height">
            <div className="product-box">
              <Link to={"/yokohamaPage/"+ props.product.id}>
                <div className="item-img">
                  <img  className="product-desc-style img-respnsive" style={{width:'100%',height:'auto'}} src={props.product.tyreImage[0]} alt=""/>
                </div>
              </Link> 
                <div className="product-info">
                  <img className="img-respnsive" src={props.product.brandImage[0]} alt="brand"/>
                  <div style={{paddingTop:'5px'}}>
                    {props.product.title}
                  </div>
                  <div className='description'>
                   <span style={{fontSize:'14px'}}> {props.product.description}</span>
                  </div>
                  <div className='price'>
                    <h4 style={{fontSize:'18px'}}>
                      <span style={{color:'#d61d28'}}>
                       <img src={rupee1} style={{height:'18px',width:'18px'}} alt=""/>{props.product.price}
                      </span>
                    </h4>
                  </div>
                   <div>
                    <Button className='cart-button'>Add to Cart</Button>
                   </div>
                </div>
            </div>
          </Col>
          
        </>
    )
}