import { ThemeProvider } from 'react-bootstrap';
import { BrowserRouter, Route, Routes, } from 'react-router-dom';
import './App.css';
import Home from './Pages/Dashboard/Home';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './Components/Header/Header';
import Footer from './Components/Footer/Footer';
import Products from './Pages/Products/Products';
import ProductDetail from './Pages/ProductDetail/ProductDetail';
import AboutUs from './Pages/AboutUs/AboutUs';
import PrivacyPolicy from './Pages/PrivacyPolicy/PrivacyPolicy';
import Terms from './Pages/Terms/Terms';
import OrderRefund from './Pages/OrderAndRefund/OrderRefund';
import Blogs from './Pages/Blogs/Blogs';
import Contact from './Pages/Contact/Contact';
import LogIn from './Pages/LogIn/LogIn';
import SignUp from './Pages/SignUp/SignUp';
import Bridgestone from './Pages/Bridgestone/Bridgestone';
import BridgestoneDetail from './Pages/Bridgestone/BridgestoneDetail';
import Ceat from './Pages/Ceat/Ceat';
import CeatDetail from './Pages/Ceat/CeatDetail';
import Perilli from './Pages/Perilli/Pirelli';
import PirelliDetail from './Pages/Perilli/PirelliDetail';
import YokohamaDetail from './Pages/Yokohama/YokohamaDetail';
import Yokohama from './Pages/Yokohama/Yokohama';
import CartList from './Pages/ShoppingCart/CartList';
import Profile from './Pages/Profile/Profile';


function App() {
 
  return (
     <ThemeProvider breakpoints={['xxl', 'xl', 'lg', 'md', 'sm', 'xs']}>
       
      <BrowserRouter>
     
      <Header></Header>
      {/* {window.location.pathname !== "/login" && window.location.pathname !== "/signup" ? <Header /> : ""} */}
     
      <Routes>
        <Route path='/' element={<Home></Home>}></Route>
        <Route path='/productPage' element={<Products></Products>}></Route>
        <Route path='/productPage/:id' element={<ProductDetail></ProductDetail>}></Route>
        <Route path='/aboutus' element={<AboutUs></AboutUs>}></Route>
        <Route path='/contactus' element={<Contact></Contact>}></Route>
        <Route path='/privacy-and-policy' element={<PrivacyPolicy></PrivacyPolicy>}></Route>
        <Route path='/terms-and-condition' element={<Terms></Terms>}></Route>
        <Route path='/order-and-refund' element={<OrderRefund></OrderRefund>}></Route>
        <Route path='/blogs' element={<Blogs></Blogs>}></Route>
        <Route path='/login' element={<LogIn></LogIn>}></Route>
        <Route path='/signup' element={<SignUp></SignUp>}></Route>
        <Route path='/bridgestonePage' element={<Bridgestone></Bridgestone>}></Route>
        <Route path='/bridgestonePage/:id' element={<BridgestoneDetail></BridgestoneDetail>}></Route>
        <Route path='/ceatPage' element={<Ceat></Ceat>}></Route>
        {/* <Route path='/mycart' element={<Cart></Cart>}></Route> */}
        <Route path='/mycart' element={<CartList></CartList>}></Route>
        <Route path='/ceatPage/:id' element={<CeatDetail></CeatDetail>}></Route>
        <Route path='/pirelliPage' element={<Perilli></Perilli>}></Route>
        <Route path='/pirelliPage/:id' element={<PirelliDetail></PirelliDetail>}></Route>
        <Route path='/yokohamaPage' element={<Yokohama></Yokohama>}></Route>
        <Route path='/yokohamaPage/:id' element={<YokohamaDetail></YokohamaDetail>}></Route>
        <Route path='/profile' element={<Profile></Profile>}></Route>
      </Routes>
     
      <Footer></Footer>
       {/* {window.location.pathname !== "/login" && window.location.pathname !== "/signup" ? <Footer /> : ""} */}
      
      </BrowserRouter>
     
    </ThemeProvider>
  );
}

export default App;
