import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getYokohamaDetails } from '../Redux/Action/Action';
import YokohamaDesc from './YokohamaDesc';

 function YokohamaDetail(props){
    const {id}=useParams();

    useEffect(()=>{
        fetch('http://localhost:3000/Yokohama/'+id)
        .then(response=>response.json())
        .then((res)=>{
            props.setYokohamaDetails(res);
        })
    },[])  
    return(
        <>
        
            {
                props.yokohamaDetails && 
                <YokohamaDesc isProductDetails={true} product={props.yokohamaDetails}></YokohamaDesc>
            }
         
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        yokohamaDetails:state.productFeature.yokohamaDetails
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setYokohamaDetails: (payload) => dispatch(getYokohamaDetails(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(YokohamaDetail)