import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getCeatDetails } from '../Redux/Action/Action';
import CeatDesc from './CeatDesc';

 function CeatDetail(props){
    const {id}=useParams();

    useEffect(()=>{
        fetch('http://localhost:3000/Ceat/'+id)
        .then(response=>response.json())
        .then((res)=>{
            props.setCeatDetails(res);
        })
    },[])  
    return(
        <>
        
            {
                props.ceatDetails && 
                <CeatDesc isProductDetails={true} product={props.ceatDetails}></CeatDesc>
            }
         
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        ceatDetails:state.productFeature.ceatDetails
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setCeatDetails: (payload) => dispatch(getCeatDetails(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CeatDetail)