import React from 'react';
import { Col,Row,Container, Button } from "react-bootstrap";
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import Table from 'react-bootstrap/Table';
import bin from '../../Images/bin.png';
import chevron from '../../Images/right.png';
import { useCart } from 'react-use-cart'
import './CartList.css';


export default function CartList(){
  
  const{
    isEmpty,
    items,
    cartTotal,
    updateItemQuantity,
    removeItem,
   
}=useCart();
    return(
        <React.Fragment>
             <div className="product-page-header">
                <Container>
                  <Row>
                    <Col md={12} >
                      <Breadcrumb >
                        <Breadcrumb.Item href="/" >
                          <div className="product-page-style">HOME</div> 
                        </Breadcrumb.Item>
                        <img  className="product-page-style" src={chevron} alt="right" style={{marginLeft:'12px'}}/>
                        <div className="product-page-style" style={{marginLeft:'12px'}}>MY CART</div>
                      </Breadcrumb>
                    </Col>
                  </Row>
                </Container>
              </div>
              <div>
                <Container>
                   <h1 className='my-5 text-center'>
                      {isEmpty && 'Your cart is empty'}
                   </h1> 
                  <Row>
                    <Col>
                      <h4 className="cart-title1">Your Shopping Cart</h4>
                      <div className='cart-table'>
                      <Table responsive='sm' bordered >
                        <thead>
                          <tr className='tr-css'>
                            <th>Cart Item</th>
                            <th>Quantity</th>
                            <th>Item Price</th>
                            <th>Discount</th>
                            <th>Total</th>
                          </tr>
                        </thead>
                        <tbody>
                         {items.map((item,index)=>{
                             return(    
                              <>
                              <tr>
                                <td style={{ display: 'flex' }}>
                                  <img className="img-responsive cart-image-style" src={item.tyreImage} alt="tyre" />
                                  <br />
                                  <div>
                                    <h1 className="cart-title" style={{ fontSize: '24px', color: "#337ab7" }}>{item.name}</h1>
                                    <p>{item.head1}</p>
                                  </div>
                                </td>
                                <td>
                                  <span>
                                    <Button className="quantity-style" variant="secondary" size="sm" onClick={() => updateItemQuantity(item.id, item.quantity + 1)}>
                                      +
                                    </Button>
                                    <span style={{ margin: '10px', padding: '2px 8px', textAlign: 'center' }}>{item.quantity}</span>
                                    <Button className="quantity-style1" variant="secondary" size="sm" onClick={() => updateItemQuantity(item.id, item.quantity - 1)}>
                                      -
                                    </Button>
                                  </span>
                                </td>
                                <td>{item.price}</td>
                                <td></td>
                                <td>{item.price}</td>
                                <td className='delete-cart-style' style={{borderRadius:'0 23px 22px 0'}}>
                                  <div onClick={() => removeItem(item.id)}>
                                    <img src={bin} style={{height:'24px',width:'24px'}}  alt="delete"/>
                                  </div>
                                </td>
                              </tr>
                             <tr>
                                  <td colSpan={3} style={{borderBottom:'none'}}></td>
                                  <td>
                                    <b>Grand Total</b>
                                  </td>
                                  <td>{cartTotal}</td>
                              </tr>
                              </>
                          )
                        })} 
                        </tbody>
                      </Table>
                      </div>
                  
                    </Col>
                  </Row>
                </Container>
              </div>
        </React.Fragment>
    )
 }