import React, { useEffect } from 'react';
import { Container, Row } from "react-bootstrap";
import { connect } from 'react-redux';
import { getPirelliDetails, getPirelliList } from '../Redux/Action/Action';
import PirelliItem from './PirelliItem';

 function Pirelli(props){

    useEffect(()=>{
            fetch('http://localhost:3000/Pirelli')
            .then(response=>response.json())
            .then((res)=>{
                props.setPirelliList(res);
            })
    },[])
    return(
        <>
         <Container>
            <Row>
                {
                   props.pirelliList && props.pirelliList.map((item)=>{
                        return  <PirelliItem product={item}></PirelliItem> 
                    })
                }
               
            </Row>
         </Container>
        </>
    )
}
    const mapStateToProps=(state)=>{
        return{
            pirelliList:state.productFeature.pirelliList
        }
    }
    const mapDispatchToProps=(dispatch)=>{
        return{
            setPirelliList:(payload) => dispatch(getPirelliList(payload)),
            setPirelliDetails:(payload) => dispatch(getPirelliDetails(payload))
        }
    }

export default connect(mapStateToProps,mapDispatchToProps)(Pirelli)