import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getBridgestoneDetails} from '../Redux/Action/Action';
import BridgestoneDesc from './BridgestoneDesc';

 function BridgestoneDetail(props){
    const {id}=useParams();

    useEffect(()=>{
        fetch('http://localhost:3000/Bridgestone/'+id)
        .then(response=>response.json())
        .then((res)=>{
            props.setBridgestoneDetails(res);
        })
    },[])  
    return(
        <>
        
            {
                props.bridgestoneDetails && 
                <BridgestoneDesc isProductDetails={true} product={props.bridgestoneDetails}></BridgestoneDesc>
            }
         
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        bridgestoneDetails:state.productFeature.bridgestoneDetails
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setBridgestoneDetails: (payload) => dispatch(getBridgestoneDetails(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(BridgestoneDetail)