import React from 'react';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import chevron from '../../Images/right.png';
import { Col,Row,Container } from "react-bootstrap";
import './ProductDescription.css';
import SubHeaderText from '../Reusable/SubHeaderText';

export default function ProductDescription(props){
    return(
        <>
          {
             props.Bridgestone &&
            <React.Fragment>
            <div className="product-page-header">
              <Container>
                <Row>
                  <Col md={12} >
                    <Breadcrumb >
                      <Breadcrumb.Item href="/" >
                        <div className="product-page-style">Home</div> 
                      </Breadcrumb.Item>
                      <img  className="product-page-style" src={chevron} alt="right" style={{marginLeft:'12px'}}/>
                      <div className="product-page-style" style={{marginLeft:'12px'}}>BRIDGESTONE</div>
                    </Breadcrumb>
                  </Col>
                </Row>
              </Container>
           </div>
           <div className='product-desc'>
             <Container>
               <Row>
                <h1 style={{textAlign:'center',fontSize:'22px',fontWeight:600,paddingBottom:'20px'}} 
                  className={"subHeader-text"}>{props.Bridgestone.head}</h1>
                <div className='productDesc-info'>
                  <Col md={4} sm={12} pb-50 xs={12} >
                    <img className="img-respnsive productDesc-img" src={props.product.image[0]} alt=""/>
                  </Col>
                  {/* <Col md={4} xs={12} sm={12}>
                    <img className="img-respnsive productDesc-brand" src={props.product.brands[0]} alt="brand"/>
                    <h2>{props.product.feature.brandName}</h2>
                  </Col>
                  <Col md={4} xs={12} sm={12}>
                    <img className="img-respnsive " src={props.product.brands[0]} alt="brand" />
                  </Col> */}
                </div>
               </Row>
             </Container>
           </div>
          </React.Fragment>
         }
         
        
        </>
    )
}