import React, { useEffect } from 'react';
import { Container, Row,Col, Button } from "react-bootstrap";
import './SubHeader.css';
import phone from '../../Images/phone.png';
import lock from '../../Images/lock-fill.svg';
import person from '../../Images/person-fill.svg';
import {  useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import {  setLoginSuccess, setRegisterSuccess } from '../../Pages/Redux/Action/Action';



export default function SubHeader(props){
    const navigate=useNavigate();
    const dispatch=useDispatch();
    let isLogin=useSelector((state)=>state.productFeature.isLogin);
    let isRegister=useSelector((state)=>state.productFeature.isRegister);
    let userProfile=useSelector((state)=>state.productFeature.userProfile);
   
     
    useEffect(()=>{
        let isLoggedIn=localStorage.getItem("isLoggedIn");
        if(isLoggedIn === "true"){
           dispatch(setLoginSuccess(true))
        }
      },[])
      
    useEffect(()=>{
         let isRegisterIn=localStorage.getItem("isRegister");
         if(isRegisterIn === "true"){
          dispatch(setRegisterSuccess(true))
         }
        },[])
  
    const onSelectFn=(event)=>{
       navigate(event)
    }
    const onLogout=()=>{
      localStorage.removeItem("isLoggedIn");
      localStorage.removeItem("isRegister");
      localStorage.removeItem("user");
      window.location.reload();
    
      
    }
    const onselectProfile=()=>{
      navigate('/profile')
    }
    return(
        <>
       
        <div style={{backgroundImage:"linear-gradient(360deg,#133a65 40%,#0d4e9d 100%)",
            backgroundPosition:"left",backgroundRepeat:"none"}} className="py-2 top-header" >
          <Container>
           <Row>
            <Col  md={9}  >
                
                <img src={phone} alt="" style={{width:'14px'}}/>
                <span style={{fontSize:"16px",color:"#fff",textAlign:"center"}}>Speak to our executive</span>
                 <a href="/tel" style={{fontWeight:700,color:"#fff"}}> +91 999-982-3205</a>
                
            </Col>
            <Col  md={3}  xs={6} >
              
              {/* {
                !isLogin   &&
                <>
                  <Button variant="light" size="sm" onClick={()=>onSelectFn("login")} style={{margin:'0 5px'}} >
                    <img src={lock} alt="add item" width="14" /> Login
                  </Button>
                 <Button variant="light" size="sm" onClick={()=>onSelectFn("signup")}>
                    <img src={person} alt="add item" width="14" /> Sign Up
                  </Button>
              </>
              }  
           
              {
                isLogin   &&
                <Button variant="light" size="sm" onClick={onLogout}>
                      <img src={person} alt="add item" width="14" /> Logout
                </Button>
               
              } */}
              {
                isRegister || isLogin 
                ? <> 
                   <Button variant="light" size="sm" style={{margin:'0 5px'}} onClick={onselectProfile}>
                          <img src={person} alt="add item" width="14" />
                          {
                             userProfile && userProfile.name
                          }
                    </Button>
                   
                    <Button variant="light" size="sm" onClick={onLogout}>
                      <img src={person} alt="add item" width="14" /> Logout
                    </Button>
                    
                   </>
                : <>
                    <Button variant="light" size="sm" onClick={()=>onSelectFn("login")} style={{margin:'0 5px'}} >
                      <img src={lock} alt="add item" width="14" /> Login
                    </Button>
                    <Button variant="light" size="sm" onClick={()=>onSelectFn("signup")}>
                      <img src={person} alt="add item" width="14" /> Sign Up
                    </Button>
                 </>
              }
             
              </Col>
            </Row>
          </Container>
        </div>
       
        </>
    )
}