import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import logo from '../../Images/logo.png';
import Form from 'react-bootstrap/Form';
import cart from '../../Images/cart.svg';
import './Header.css';
import SubHeader from './SubHeader';
import { Button,Col} from 'react-bootstrap';
import bridgestone from '../../Images/bridgestone.png';
import firestone from '../../Images/firestone.jpg';
import mrf from '../../Images/mrf.png';
import michelin from '../../Images/michelin.png';
import pirelli from '../../Images/pirelli.jpg';
import yokohama from '../../Images/yokohama.png';
import ceat from '../../Images/ceat.png';
import { Link, useNavigate } from 'react-router-dom';
import { useCart } from "react-use-cart";
import { useState,useEffect } from 'react';

export default function Header(props) {
  const {isEmpty,totalItems,} = useCart();
  const navigate=useNavigate();
  const[searchItem,setSearchItem]=useState('');
  const [searchResults, setSearchResults] = useState([]);
  const onClickHandler=()=>{
    navigate('/')
  };
  const handleChange = event => {
    setSearchItem(event.target.value);
  };
 
  var tyreBrands=[
    {name:"Bridgestone",url:"/bridgestonePage"},
    {name:"Yokohama", url:'/yokohamaPage'},
    {name:"Michelin",url:'/michelinPage'},
    {name:"Pirelli",url:'/pirelliPage'},
    {name:"Ceat",url:"/ceatPage",},
  ];
  var brands=[
    {
      brand:bridgestone,
      url:"/bridgestonePage",
    },
    {
      brand:ceat,
      url:"/ceatPage",
    },
    {
      brand:yokohama,
      url:'/yokohamaPage'
    },
    {
      brand:michelin,
      url:'/michelinPage',
    },
    {
      brand:pirelli,
      url:'/pirelliPage',
    },
    {
      brand:mrf,
      url:'/pirelliPage',
    },
    {
      brand:firestone,
      url:'/pirelliPage',
    },
  ]
 
  return (
    <>
   
    <SubHeader></SubHeader>
    <Navbar collapseOnSelect expand="lg" className="navbar-style nav-default sticky-top ">
      <Container>
        <Navbar.Brand href=""><img src={logo} alt=""/></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
          <Nav className="nav-style">
            <Button className="nav-style1" onClick={onClickHandler}>Home</Button>
          </Nav>
          <Nav className="nav-style">
            <NavDropdown title="Brand Types" id="my-responsive-nav" >
              <Col lg={12}>
                {
                 brands.map((item,index)=>{
                  return(
                    <li>
                       {
                          <Link to={item.url} className='dropdown-link' >
                            <div className='Img-wrapper'>
                              <img className="img-responsive" src={item.brand} alt=""/>
                            </div>
                          </Link>
                        }
                     </li>
                  )
                })
              }
              </Col>
            </NavDropdown>
          </Nav>
          <Nav className="nav-style">
            <NavDropdown title="Car Brand" id="my-responsive-nav">
          
             <NavDropdown.Item href="#">type1</NavDropdown.Item>
              <NavDropdown.Item href="#">type1</NavDropdown.Item>
              <NavDropdown.Item href="#">type1</NavDropdown.Item>
             
             
            </NavDropdown>
          
          </Nav>
         
          <Nav className="nav-style cart-style">
            <Link to="/mycart" style={{color:'black'}}>
              <img src={cart} alt=""/>
              {!isEmpty &&<span>({totalItems})CART</span>}
            </Link>
          </Nav>
          <Nav>
            <Form>
              <Form.Control 
                type="text" 
                placeholder="Search" 
                className="me-2 search-style" 
                aria-label="Search"
                value={searchItem}
                onChange={handleChange}/>
                {
                    searchResults.map((item,index)=>{
                      return(
                        <li style={{display:'none'}}>
                          {item.url
                             ? <Link to={item.url} style={{color:'white'}}>{item.name}</Link>
                             : <a href={item.url} style={{color:'white'}}>{item.name}</a>
                           } 
                        </li>
                    )})
              }
            </Form> 
          </Nav>  
        </Navbar.Collapse>
      </Container>
    </Navbar>
  </>
  );
}
