import React, { useEffect } from 'react';
import { Container, Row } from "react-bootstrap";
import { connect } from 'react-redux';
import {  getCeatDetails, getCeatList } from '../Redux/Action/Action';
import CeatItem from './CeatItem'

 function Ceat(props){
    
    useEffect(()=>{
            fetch('http://localhost:3000/Ceat')
            .then(response=>response.json())
            .then((res)=>{
                props.setCeatList(res);
            })
    },[])
    return(
        <>
         <Container>
            <Row>
                {
                 props.ceatList && props.ceatList.map((item)=>{
                    return <CeatItem product={item} />
                        
                    })
                }
               
            </Row>
         </Container>
        </>
    )
}
    const mapStateToProps=(state)=>{
        return{
            ceatList:state.productFeature.ceatList
        }
    }
    const mapDispatchToProps=(dispatch)=>{
        return{
            setCeatList:(payload) => dispatch(getCeatList(payload)),
            setCeatDetails:(payload) => dispatch(getCeatDetails(payload)),
          
        }
    }

export default connect(mapStateToProps,mapDispatchToProps)(Ceat)