import React from 'react';
import { Row,Col, Container, Button} from 'react-bootstrap';
import hand from '../../Images/hand-right.png';
import './KnowMore.css';

export default function KnowMore(){
    return(
        <>
        <div className="watch-style">
            <Container>
              <Row>
                <Col xs={10} md={6}  pt-50>
                  <p>How to Get Tyres fitted
                    <br></br>
                    at your doorstep !
                  </p>
                </Col>
               
                <Col  xs={10} md={6} >
                  <Button variant="light" size='lg' style={{fontWeight:700,padding:'12px 33px',fontSize:18,boxShadow:'0 5px 15px rgb(0 0 0 / 19%)'}}>
                  <img src={hand} alt="" width='20'/> Know More</Button>
                </Col>
              </Row>
            </Container>
          </div>
        </>
    )
}