import { GET_BRIDGESTONE_DETAILS, GET_BRIDGESTONE_LIST, GET_CEAT_CART_LIST, GET_CEAT_DETAILS, GET_CEAT_LIST, GET_PIRELLI_DETAILS, GET_PIRELLI_LIST, GET_PRODUCT_DETAILS, GET_PRODUCT_LIST, GET_YOKOHAMA_DETAILS, GET_YOKOHAMA_LIST, LOGIN_SUCCESS, REGISTER_SUCCESS, SET_EMAIL_ADDRESS1, SET_LOGIN_EMAIL_ADDRESS, SET_LOGIN_PASSWORD, SET_MOBILE_NUMBER, SET_NAME, SET_PASSWORD1, SET_USER_PROFILE} from "../Constants/Constants";
import { InitialState } from "../Store/InitialState";

export default function ProductReducer(state=InitialState,action){
   switch (action.type) {
    case SET_LOGIN_EMAIL_ADDRESS:
        return{
            ...state,
            userLogin:{
                ...state.userLogin,
                email:action.payload
            }
        }
    case SET_LOGIN_PASSWORD:
        return{
            ...state,
            userLogin:{
                ...state.userLogin,
                password:action.payload
            }
        }   
     case LOGIN_SUCCESS:
        return{
            ...state,
            isLogin:true
    }   
    case SET_NAME:
        return{
            ...state,
            userSignup:{
                ...state.userSignup,
                name:action.payload
            }
        }
    case SET_EMAIL_ADDRESS1:
        return{
            ...state,
            userSignup:{
                ...state.userSignup,
                email1:action.payload
            }
        }
    case SET_MOBILE_NUMBER:
        return{
            ...state,
            userSignup:{
                ...state.userSignup,
                mobileNo:action.payload
            }
        }
    case SET_PASSWORD1:
        return{
            ...state,
            userSignup:{
                ...state.userSignup,
                password1:action.payload
            }
        }
    case REGISTER_SUCCESS:
        return{
             ...state,
             isRegister:true
    }   
    case GET_PRODUCT_LIST:
        return{
            ...state,
            productList:action.payload
        }
    case GET_PRODUCT_DETAILS:
        return{
            ...state,
            productDetails:action.payload
        }
    case GET_BRIDGESTONE_LIST:
        return{
            ...state,
            bridgestoneList:action.payload
        }
    case GET_BRIDGESTONE_DETAILS:
        return{
            ...state,
            bridgestoneDetails:action.payload
        }
    case GET_CEAT_LIST:
            return{
                ...state,
                ceatList:action.payload
            }
    case GET_CEAT_DETAILS:
            return{
                ...state,
                ceatDetails:action.payload
            }    
    case GET_PIRELLI_LIST:
            return{
                ...state,
                pirelliList:action.payload
            }
    case GET_PIRELLI_DETAILS:
            return{
                ...state,
                pirelliDetails:action.payload
            }    
    case GET_YOKOHAMA_LIST:
            return{
                ...state,
                yokohamaList:action.payload
            }
    case GET_YOKOHAMA_DETAILS:
            return{
                ...state,
                yokohamaDetails:action.payload
           }  
    case GET_CEAT_CART_LIST:
        return{
            ...state,
            ceatCartList:action.payload
    }  
    case SET_USER_PROFILE:
        return{
            ...state,
            userProfile:action.payload
    }
   
    default:
        return{
            ...state,
        }

   }
}