import React, { useEffect } from 'react';
import { Container, Row } from "react-bootstrap";
import { connect } from 'react-redux';
import { getBridgestoneList, getBridgestoneDetails } from '../Redux/Action/Action';
import BridgestoneItem from './BridgestoneItem';

 function Bridgestone(props){

    useEffect(()=>{
            fetch('http://localhost:3000/Bridgestone')
            .then(response=>response.json())
            .then((res)=>{
                props.setBridgestoneList(res);
            })
    },[])
    return(
        <>
         <Container>
            <Row>
                {
                   props.bridgestoneList && props.bridgestoneList.map((item)=>{
                        return  <BridgestoneItem product={item}></BridgestoneItem> 
                    })
                }
               
            </Row>
         </Container>
        </>
    )
}
    const mapStateToProps=(state)=>{
        return{
            bridgestoneList:state.productFeature.bridgestoneList
        }
    }
    const mapDispatchToProps=(dispatch)=>{
        return{
            setBridgestoneList:(payload) => dispatch(getBridgestoneList(payload)),
            setBridgestoneDetails:(payload) => dispatch(getBridgestoneDetails(payload))
        }
    }

export default connect(mapStateToProps,mapDispatchToProps)(Bridgestone)