import React from 'react';
import { Col, Container,Row } from "react-bootstrap";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { connect } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import logo from '../../Images/logo.png';
import { setEmailAddress1, setMobileNumber, setName, setPassword1, setRegisterSuccess } from "../Redux/Action/Action";


 function SignUp(props){

  const navigate=useNavigate();
  
  const handleSubmit = (e) => {
      e.preventDefault();
      return true
     
  }
  
  const onClickHandler=()=>{
      var signupId={
        ...props.userSignup,
        id:1
      }
        fetch("http://localhost:3000/signups",{
          method:"POST",
          body:JSON.stringify(signupId)
        }).then((response)=>response.json())
        .then((res)=>{
          console.log(res);
          props.setRegisterSuccess(true);
          localStorage.setItem("isRegister",true);
          // localStorage.setItem("user",JSON.stringify(res));
          localStorage.setItem("user",JSON.stringify(props.userSignup));
          navigate("/")
          
        },[])
        return false
       
    }
    

    const onChangeName=(e)=>{
      props.setName(e.target.value)
    }
    const onChangeEmail=(e)=>{
      props.setEmail(e.target.value)
    }
    const onChangeNumber=(e)=>{
      props.setMobileNo(e.target.value)
    }
    const onChangePassword=(e)=>{
      props.setPassword(e.target.value)
    }
    return(
        <>
          <div className="login-wrapper">
            <Container>
                <Row>
                   <Col md={12} style={{textAlign:'center',padding:'14px'}}>
                     <img src={logo} style={{width:'130px'}} alt="tyrewale"/>
                  </Col>
                  <Col md={12} className="login-style">
                  <Form onSubmit={handleSubmit} >
                    <Form.Group className="mb-3" controlId='name'>
                      <Form.Label>Your name</Form.Label>
                      <Form.Control size="sm" type="text" value={props.userSignup.name} onChange={onChangeName} required/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId='email'>
                      <Form.Label>Email Address</Form.Label>
                      <Form.Control size="sm" type="email"  value={props.userSignup.email1} onChange={onChangeEmail}  required/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId='mobileNo'>
                        <Form.Label>Mobile number</Form.Label>
                        <Form.Control size="sm" type="tel"  pattern="0-9" value={props.userSignup.mobileNo} maxLength='10'  onChange={onChangeNumber} required/>
                        <Form.Control.Feedback type="invalid">
                        Mobile can not be blank.
                      </Form.Control.Feedback>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId='password'>
                        <Form.Label>Password</Form.Label>
                        <Form.Control size="sm" type="password"  value={props.userSignup.passward1} onChange={onChangePassword}  required/>
                    </Form.Group>
                  
                    <div className="mb-3">
                      <Button as="input" type="submit" value="Signup" className="login-button" onClick={onClickHandler}/>
                    </div>
                    <div className='tbs-divider'></div>
                    <div>
                      <span className="login-text">Don't have an account ?&nbsp;<Link to="/login">Login</Link></span>
                    </div>
                    </Form>
                  </Col>
                </Row>
            </Container>
          </div>
        </>
    )
}
const mapStateToProps=(state)=>{
  return{
    userSignup:state.productFeature.userSignup
  }
}
const mapDispatchToProps=(dispatch)=>{
  return{
    setName:(payload)=>dispatch(setName(payload)),
    setEmail:(payload)=>dispatch(setEmailAddress1(payload)),
    setMobileNo:(payload)=>dispatch(setMobileNumber(payload)),
    setPassword:(payload)=>dispatch(setPassword1(payload)),
    setRegisterSuccess:(payload)=>dispatch(setRegisterSuccess(payload))
  }
}
export default connect(mapStateToProps,mapDispatchToProps)(SignUp)