import React from 'react';
import { Col } from "react-bootstrap";
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import './Ceat.css';
import { useCart } from 'react-use-cart';

export default function CeatItem(props){
  const {addItem}=useCart();
   const addToCart=()=>{
     addItem(props.product);
   }
   return(
        <>
          
          <Col xs={6} md={3} lg={3} sm={4} className="product product-height">
            <div className="product-box">
              <Link to={"/ceatPage/"+ props.product.id} >
                <div className="item-img">
                  <img  className="product-desc-style img-respnsive" style={{width:'100%',height:'auto'}} src={props.product.tyreImage[0]} alt=""/>
                </div>
              </Link> 
                <div className="product-info">
                  <img className="img-respnsive" src={props.product.brandImage[0]} alt="brand"/>
                  <div style={{paddingTop:'5px'}}>
                    {props.product.title}
                  </div>
                  <div className='description'>
                   <span style={{fontSize:'14px'}}> {props.product.description}</span>
                  </div>
                  <div className='price'>
                    <h4 style={{fontSize:'18px'}}>
                      <span style={{color:'#d61d28'}}>
                       &#8377;&nbsp;{props.product.price}
                      </span>
                    </h4>
                  </div>
                  <div>
                    
                    <Button  className='cart-button' onClick={addToCart}>Add to Cart</Button>
                  
                  </div>
                </div>
            </div>
          </Col>
          
        </>
    )
}
