import React from 'react';
import { Row,Col} from 'react-bootstrap';
import './TextScroll.css';

const car=require('../../Images/car.png');
const wheel=require('../../Images/wheels.png');
export default function TextScroll(){
    return(
        <>
           <Row>
            <Col>
              <marquee behavior="scroll" direction="left">
                <div className='marquee-style'>
                  <img src={car} alt="car tyres" style={{width:'55px'}}/>
                  <span>One stop place to buy car tyres online</span>
                </div>
                <div className='marquee-style'>
                  <img src={wheel} alt="car tyres" style={{width:'29px',animation: '3s rotateRight infinite linear'}}/>
                  <span>We provide complete Tyre Solutions For All Luxury cars Like BMW.</span>
                </div>
                <div className='marquee-style'>
                  <img src={wheel} alt="car tyres" style={{width:'29px',animation: '3s rotateRight infinite linear'}}/>
                  <span>Online tyre price, Best place to buy tyres online.</span>
                </div>
                <div className='marquee-style'>
                  <img src={car} alt="car tyres" style={{width:'55px'}}/>
                  <span> One stop place to buy car tyres online</span>
                </div>
                <div className='marquee-style'>
                  <img src={wheel} alt="car tyres" style={{width:'29px',animation: '3s rotateRight infinite linear'}}/>
                  <span>We provide complete Tyre Solutions For All Luxury cars Like BMW.</span>
                </div>
                <div className='marquee-style'>
                  <img src={car} alt="car tyres" style={{width:'55px'}}/>
                  <span>Buy tyres, Buy car tyres, Buy tyres online</span>
                </div>
                <div className='marquee-style'>
                  <img src={wheel} alt="car tyres" style={{width:'29px',animation: '3s rotateRight infinite linear'}}/>
                  <span>Online tyre price, Best place to buy tyres online.</span>
                </div>
              </marquee>
            </Col>
          </Row>
        </>
    )
}