import React from 'react';
import { Col } from "react-bootstrap";
import { Link } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import './ProductItem.css';

export default function ProductItem(props){
    return(
        <>
          <Col xs={6} md={3} lg={3} sm={4} className="product product-height">
            <div className="product-box">
              <Link to={"/productPage/"+props.product.id}>
                <div className="item-img">
                  <img  className="product-image-style img-respnsive" src={props.product.image[0]} alt=""/>
                </div>
              </Link> 
                <div className="product-info">
                  <img className="img-respnsive" src={props.product.brands[0]} alt="brand"/>
                  <div style={{paddingTop:'5px'}}>
                    {props.product.title}
                  </div>
                  <div className='description'>
                    {props.product.description}
                  </div>
                  <div className='price'>
                    <span style={{color:'red'}}> ${props.product.price}</span>
                   </div>
                   <div>
                    <Button className='cart-button'>Add to Cart</Button>
                   </div>
                </div>
            </div>
          </Col>
          
        </>
    )
}