import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import HeaderText from '../Reusable/HeaderText';
import './Footer.css';

const facebook=require('../../Images/facebook.png');
const pin=require('../../Images/pinterest.png');
const insta=require('../../Images/insta.png');
const link=require('../../Images/link.png');
const twitter=require('../../Images/twitter.png');
export default function Footer(){
  var carBrands=[
    {name:"BMW"},
    {name:"Tata"},
    {name:"Honda"},
    {name:"Toyota"},
    {name:"Maruti"},
    {name:"Chevrolet"},
    {name:"KIA"},
    {name:"Ford"},
    {name:"Hyundai"},
    {name:"Datsun"},
    {name:"Fiat"},
  ]
  var tyreBrands=[
    {name:"Bridgestone"},
    {name:"Yokohama"},
    {name:"Michelin"},
    {name:"Continental"},
    {name:"Apollo"},
    {name:"Ceat"},
    {name:"Goodyear"},
    {name:"Yokohama"},
  ]
  var links=[
    {
      name:"About Us",
      url:"/aboutus",
    },
    {
      name:"Contact Us",
      url:"/contactus",
    },
    {
      name:"Privacy Policy",
      url:"/privacy-and-policy",
    },
    {
      name:"Trems & Conditions",
      url:"/terms-and-condition",
    },
    {
      name:"Order Refunds",
      url:"/order-and-refund",
    },
    {
      name:"Our Blogs",
      url:"/blogs",
    },
    
  ]
 const social=[
  {icon: <img src={facebook}  style={{height:30,width:30,border:'1px solid'}} alt="social"/>},
  {icon:<img src={pin} style={{height:30,width:30,border:'1px solid'}}alt="social"/>},
  {icon:<img src={insta} style={{height:30,width:30,border:'1px solid'}}alt="social"/>},
  {icon:<img src={link} style={{height:30,width:30,border:'1px solid'}}alt="social"/>},
  {icon:<img src={twitter} style={{height:30,width:30,border:'1px solid'}}alt="social"/>},
 ]
    return(
        <React.Fragment>
        <footer className='webintern-footer'>
          <div className='container-style1'>
          <Container >
            <Row>
              <Col xs={6} sm={6} md={3} >
                <HeaderText className={'footer-title'} name={"Car Brands"} style={{color:'white'}}/>
                <Row>
                <Col md={6} style={{fontSize:'15px'}} className="list-style">
                  {
                    carBrands.splice(0,7).map ((item,index)=>{
                      return(
                        <li key={"col1_"+index}>
                          <a href="#" style={{color:'white'}}>{item.name}</a>
                        </li>
                    )})
                  }
                </Col>
                <Col md={6} style={{fontSize:'15px'}} className="list-style">
                 {
                    carBrands.map((item,index)=>{
                      return(
                        <li key={"col2_"+index}>
                          <a href="#" style={{color:'white'}}>{item.name}</a>
                        </li>
                    )})
                  }
                </Col>
                </Row>
              </Col>
              <Col xs={6} sm={6} md={4} >
                <HeaderText className={'footer-title'} name={"Tyre Brands"} style={{color:'white'}}/>
                <Row>
                  <Col md={6} style={{fontSize:'15px'}} className="list-style">
                  {
                    tyreBrands.splice(0,5).map((item,index)=>{
                      return(
                        <li key={"col1_"+index}>
                          <a href="#" style={{color:'white'}}>{item.name}</a>
                        </li>
                    )})
                  }
                  </Col>
                  <Col md={6} style={{fontSize:'15px'}} className="list-style">
                  {
                    tyreBrands.map((item,index)=>{
                      return(
                        <li key={"col2_"+index}>
                          <a href="#" style={{color:'white'}}>{item.name}</a>
                        </li>
                    )})
                  }
                  </Col>
                </Row>
              </Col>
              <Col xs={6} sm={6} md={3} >
                <HeaderText className={'footer-title'} name={"Important Links"} style={{color:'white'}}/>
                <Row>
                  <Col className="list-style">
                    {
                      links.map((item,index)=>{
                      return(
                        <div style={{marginBottom:10}}>
                        <li key={"col1_"+index} >
                           {item.url
                             ? <Link to={item.url} style={{color:'white'}}>{item.name}</Link>
                             : <a href={item.url} style={{color:'white'}}>{item.name}</a>
                           } 
                        </li>
                        </div>
                      )})
                    }
                  </Col>
                  </Row>
              </Col>
              <Col xs={6} sm={6} md={2}>
                <HeaderText className={'footer-title'} name={"Support"} style={{color:'white'}}/>
                <Row>
                  <Col>
                    <span style={{marginBottom:0}}>
                      <a href="#" style={{color:'white'}}>+91 999-982-3205</a>
                    </span> 
                    <br/>
                    <br/>
                    <span style={{marginBottom:0}}>
                      <a href="#" style={{color:'white'}}>Support@tyrewaale.com</a>
                    </span>
                    <HeaderText className={'footer-title'} name={"Follow Us"} style={{color:'white'}}/>
                     <div  className="list-style" style={{display:"inline-flex"}}>
                        {social.map((item, index) => (
                          <div  style={{marginRight:8}}>
                            <li key={"row1" +index}>
                             <a href="#"> {item.icon}</a>
                            </li>
                          </div>
                      ))}
                      </div>
                    </Col>
                </Row>
              </Col>
            </Row>
          </Container>
          </div>
          <footer className='footer2'>
            <Container>
              <Row>
                <Col sm={12} style={{textAlign:'center'}}>
                  <p className='copyright' style={{color:'white'}}>&copy; 2017- 2023 Tyrewaale | All Rights Reserved</p>
                </Col>
              </Row>
            </Container>
          </footer>
        </footer>
         
    </React.Fragment>
    )
}