import React from 'react';
import { Container,Row,Col } from 'react-bootstrap';
import './FeatureInfo.css';


export default function FeatureInfo(props){
    
    return(
        <>
         <div className='our-service'>
           <Container>
            <Row>
              {
                props.informationText.map((item,index)=>{
                    return(
                        < Col xs={12} md={6} sm={12} lg={4}>
                            <div className='textbox-style'>
                               {/* <div className='feature-icon-style '>
                                  <img src={item.Img} alt="" className='feature-img-style'/>
                               </div> */}
                               <div className='message-style'>
                                 <h3>{item.title}</h3>
                                 <p>{item.text}</p>
                               </div>
                            </div>
                        </Col>
                    )
                })
              }
            </Row>
           </Container>
         </div>
        </>
    )
}