import React, { useEffect } from 'react';
import { Container, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import ProductDescription from '../../Components/ProductDescription/ProductDescription';
import { getProductDetails } from '../Redux/Action/Action';

 function ProductDetail(props){
    const {id}=useParams();

    useEffect(()=>{
        fetch('http://localhost:3000/products/'+id)
        .then(response=>response.json())
        .then((res)=>{
            props.setProductDetails(res);
        })
    },[])  
    return(
        <>
        
            {
                props.productDetails && 
                <ProductDescription product={props.productDetails}></ProductDescription>
            }
         
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        productDetails: state.productFeature.productDetails
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setProductDetails: (payload) => dispatch(getProductDetails(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ProductDetail)