import React, { useEffect } from 'react';
import { Col,Row,Container, Button } from "react-bootstrap";
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import chevron from '../../Images/right.png';
import { connect } from 'react-redux';
import user from '../../Images/default.png';
import user1 from '../../Images/user1.png';
import lock from '../../Images/lock-fill.svg';
import bag from '../../Images/shopping-bag.png';
import sign from '../../Images/sign-in.png';
import list from '../../Images/list.png';
import ListGroup from 'react-bootstrap/ListGroup';
import Table from 'react-bootstrap/Table';
import Tab from 'react-bootstrap/Tab';
import Form from 'react-bootstrap/Form';
import './Profile.css';
import { setUserProfile } from '../Redux/Action/Action';
import { useNavigate } from 'react-router-dom';

function Profile(props){
 
 const navigate=useNavigate();
 useEffect(()=>{
        
        let user=localStorage.getItem("user");
        user=JSON.parse(user);
        props.setUserProfile(user);
        // fetch('http://localhost:3000/users/'+user.id)
        // .then(response=>response.json())
        // .then((res)=>{
        //     props.setUserProfile(res);
        // })
        
    },[])
    const uploadedImage = React.useRef(null);  
    const imageUploader = React.useRef(null);
    const handleImageUpload = e => {
        const [file] = e.target.files;
        if (file) {
            const reader = new FileReader();
            const {current} = uploadedImage;
            current.file = file;
            reader.onload = (e) => {
                current.src = e.target.result;
            }
            reader.readAsDataURL(file);
          }
      };
      const onLogoutHandler=()=>{
        localStorage.removeItem('user');
          navigate('/')
      }
    return(
        <>
           <div>
            {
                props.userProfile &&
                <React.Fragment>
                  <div className="product-page-header" style={{marginBottom:'20px'}}>
                    <Container>
                      <Row>
                        <Col md={12} >
                          <Breadcrumb >
                            <Breadcrumb.Item href="/" >
                              <div className="product-page-style">Home</div> 
                            </Breadcrumb.Item>
                            <img  className="product-page-style" src={chevron} alt="right" style={{marginLeft:'12px'}}/>
                            <div className="product-page-style" style={{marginLeft:'12px'}}>User</div>
                            <img  className="product-page-style" src={chevron} alt="right" style={{marginLeft:'12px'}}/>
                           <div className="product-page-style" style={{marginLeft:'12px'}}>{props.userProfile.name}</div>
                          </Breadcrumb>
                        </Col>
                      </Row>
                   </Container>
                </div>
                <div>
                  <Container>
                  <Tab.Container defaultActiveKey="#link1">
                    <Row>
                      <Col md={3}>
                        <div className='side-tabs'>
                          <div className='userpic-style pic-grid'>
                            <div onClick={() => imageUploader.current.click()}>
                              <img className="img-responsive" src={user} alt="user"  ref={uploadedImage}/>
                            </div>
                            <input type="file" accept="image/*" onChange={handleImageUpload} ref={imageUploader} style={{display:'none'}}/>
                          </div>
                          <h4 style={{marginTop:'30px',textAlign:'center',fontSize:'18px'}}>{props.userProfile.name}</h4>
                          <br/>
                         <ListGroup variant="flush" >
                           <ListGroup.Item className="listgroup-item" action href="#link1">
                             <img src={user1} alt="" style={{height:'15px',width:'15px',marginRight:'8px'}}/>
                               Profile
                            </ListGroup.Item>
                            <ListGroup.Item className="listgroup-item" action href="#link2">
                              <span><img src={lock} alt="" style={{height:'15px',width:'15px',marginRight:'8px'}}/></span>
                                Change Password
                            </ListGroup.Item>
                            <ListGroup.Item action className="listgroup-item" href="#link3">
                              <span><img src={list} alt="" style={{height:'15px',width:'15px',marginRight:'8px'}}/></span>
                                Update Profile
                            </ListGroup.Item>
                            <ListGroup.Item action className="listgroup-item"  href="#link4">
                               <span><img src={bag} alt="" style={{height:'15px',width:'15px',marginRight:'8px'}}/></span>
                                My Orders
                            </ListGroup.Item>
                            <ListGroup.Item action className="listgroup-item"  href="#link5" onClick={onLogoutHandler}>
                             <span><img src={sign} alt="" style={{height:'15px',width:'15px',marginRight:'8px'}}/></span>
                                Logout
                            </ListGroup.Item>
                          </ListGroup>
                        </div>
                      </Col>
                      <Col md={9}>
                       <Tab.Content className='side-tabs'>
                        <Tab.Pane eventKey="#link1">
                            <div className="side-tabs1">
                              <h4 style={{fontSize:'25px',}}>Profile</h4>
                              <h2 className='heading'>Name, ID and Email Addresses.</h2>
                              <p style={{color:'#666'}}>Below are the name and email addresses on file for your account.</p>
                               <br/>
                              <Table responsive>
                                <tbody>
                                    <tr>
                                        <th >First Name</th>
                                        <td>{props.userProfile.name}</td>
                                    </tr>
                                    <tr>
                                        <th>Email Address</th>
                                        <td>{props.userProfile.email}</td>
                                    </tr>
                                    <tr>
                                        <th>Contact Number</th>
                                        <td>{props.userProfile.mobileNo}</td>
                                    </tr>
                                    <tr>
                                        <th>Address</th>
                                        <td></td>
                                    </tr>
                                </tbody>
                              </Table>
                            </div>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#link2">
                          <div className="side-tabs1">
                            <h4 style={{fontSize:'25px',}}>Change Password</h4>
                            <h2 className='heading'>Manage Your Password</h2>
                            <p style={{color:'#666'}}>Below are the New Password and Confirm Password.</p>
                          </div>
                          <br/>
                          <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>New Password</Form.Label>
                               <Form.Control type="password" size="sm"/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Confirm Password</Form.Label>
                               <Form.Control type="password" size="sm"/>
                            </Form.Group>
                            <div style={{textAlign:'center'}}>
                              <Button type="submit" variant="dark">change password</Button>
                            </div>
                          </Form>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#link3">
                        <div className="side-tabs1">
                            <h4 style={{fontSize:'25px',}}>Update Profile</h4>
                            <h2 className='heading'>Manage Your Name, Email, Phone Number and others..</h2>
                            <p style={{color:'#666'}}>Below are the name and email addresses on file for your account.</p>
                          </div>
                          <br/>
                          <Form>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>First Name</Form.Label>
                               <Form.Control type="text" size="sm" value={props.userProfile.name}
                                 onChange={(e)=>props.setName(e.target.value)}/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Email</Form.Label>
                               <Form.Control type="email" size="sm" value={props.userProfile.email}
                                onChange={(e)=>props.setEmail(e.target.value)}/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Contact No</Form.Label>
                               <Form.Control type="text" size="sm" value={props.userProfile.mobileNo}
                                  onChange={(e)=>props.setMobileNo(e.target.value)}/>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                               <Form.Label>Address</Form.Label>
                               <Form.Control type="email" size="sm"/>
                            </Form.Group>
                            <div style={{textAlign:'center'}}>
                              <Button type="submit" variant="dark">update</Button>
                            </div>
                          </Form>
                        </Tab.Pane>
                        <Tab.Pane eventKey="#link4">
                          <div className="side-tabs1">
                            <h4 style={{fontSize:'25px',}}>Order History</h4>
                            <p style={{color:'#666'}}>Below are the recent orders</p>
                          </div>
                          <div style={{textAlign:'center',fontSize:'27px'}}>
                              <p>No results found.</p>
                            </div>
                        </Tab.Pane>
                      </Tab.Content>
                      </Col>
                    </Row>
                  </Tab.Container>
                  </Container>
                </div>
                </React.Fragment>
               
            }
            </div>
        </>
    )
}
const mapStateToProps=(state)=>{
    return{
        userProfile:state.productFeature.userProfile
    }
}

const mapDispatchToProps =( dispatch) =>{
    return {
        setUserProfile: (payload) => dispatch(setUserProfile(payload)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Profile)